<template>
  <div class="container" ref="screenDom"></div>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import * as THREE from 'three'

const screenDom = ref(null)

onMounted(() => {
  console.log('mounted')

  // 创建渲染器
  const renderer = new THREE.WebGLRenderer();
  renderer.setSize(window.innerWidth, window.innerHeight);
  screenDom.value.appendChild(renderer.domElement)

  window.addEventListener('resize', () => {
    renderer.setSize(window.innerWidth, window.innerHeight);
    screenDom.value.appendChild(renderer.domElement);
  })

  // 创建场景
  const scene = new THREE.Scene()
  scene.background = new THREE.Color(0xffffff)

  // 创建相机
  const camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 1, 1000)

  camera.lookAt(-5, -3, -10)

  // 相机位置
  camera.position.set(5, 7, 10)

  // 创建灯光
  const light = new THREE.DirectionalLight(0xffffff); // 平行光
  light.position.set(-1, 1.0, 1).normalize(); // 设置平行光的方向，从(0.5, 1.0, 0.5)->target一般(0, 0, 0)
  scene.add(light); // 将灯光添加到场景中

  function generateMesh() {
    // 类型
    const geometry = new THREE.BoxGeometry( 1, 1, 1 );
    // 材质
    const material = new THREE.MeshStandardMaterial( {color: 0x55ffff} );
    // 物体
    const cube = new THREE.Mesh( geometry, material );

    return cube
  }

  for (let k = 0; k < 3; k++) {
    for (let i = 0; i < 3; i++) {
      const cube = generateMesh()

      cube.position.set(i * 1.1, 0.5 + 1.1 * k, 0)
      scene.add(cube)
    }
  }

  // 创建地面
  const ground = new THREE.Mesh(
    new THREE.PlaneGeometry(50, 50),
    new THREE.MeshBasicMaterial({
      color: 0xcccccc,
      side: THREE.DoubleSide
    })
  );
  ground.rotation.x = -Math.PI / 2
  ground.receiveShadow = true;
  scene.add(ground);

  const raycaster = new THREE.Raycaster()
  const pointer = new THREE.Vector2()

  function onPointerMove( event ) {

    // 将鼠标位置归一化为设备坐标。x 和 y 方向的取值范围是 (-1 to +1)

    pointer.x = ( event.clientX / window.innerWidth ) * 2 - 1;
    pointer.y = - ( event.clientY / window.innerHeight ) * 2 + 1;

    // 通过摄像机和鼠标位置更新射线
    raycaster.setFromCamera( pointer, camera );

    // // 计算物体和射线的焦点
    const intersects = raycaster.intersectObjects( scene.children );

    if (intersects.length > 0 && intersects[ 0 ].object.geometry.type === 'BoxGeometry') {
      if (intersects[ 0 ].object.material.color.getHex() === 0xff0000) {
        intersects[ 0 ].object.material.color.set( 0x55ffff );
      } else {
        intersects[ 0 ].object.material.color.set( 0xff0000 );
      }
    }

    renderer.render(scene, camera)
  }

  window.addEventListener('click', onPointerMove)
  renderer.render( scene, camera );
})
</script>

